import React from "react"
import {Comment} from "./comment"

export const Comments = ({ slug, comments }) => {
  console.log("COMMENTS:")
  console.log(comments)
  return (
    <div>
      <hr />
      <h2>Comments:</h2>
      <div>
        {comments.map((value, index) => {
          return <Comment key={index} comment={value} />
        })}
      </div>
      <h3>Add a comment</h3>
      <form
        method="POST"
        action="https://bluealloy-blog.herokuapp.com/v3/entry/github/rakita/bluealloy/master/comments"
      >
        <input name="options[redirect]" type="hidden" value="rakita.github.io/about" />
        <input name="fields[slug]" type="hidden" value={slug} />
        <input name="fields[name]" type="text" placeholder="Name" required />
        <input name="fields[email]" type="email" placeholder="Email" required />
        <textarea name="fields[message]" placeholder="Comment" required />
        <button type="submit">Submit Comment</button>
      </form>
    </div>
  )
}
